*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.container {
  min-width: 320px;
  margin: 0 auto;
}

@media only screen and (max-width: 767.98px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439.98px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}



:root {
  --main-white-color: #fff;
  --main-bc-modal: #fff;
  --main-yellow-color: #eac645;
  --accent-yellow-color: #eac645;
  --main-violet-color: #4f2ee8;
  --accent-violet-color: #4f2ee8;
  --main-dark_gray-color: #202024;
  --main-black-color: #111;
  --body-background: #f6f6f6;
  --font-size-header_nav: 14px;
  --font-size-header_account: 22px;
  --font-size-categories: 16px;
  --font-size-support_header: 22px;
  --font-size-main_header: 32px;
  --font-size-cards_name: 16px;
  --font-size-cards_author: 12px;
  --font-size-cards_genres: 12px;
  --font-size-cards_text: 14px;
  --font-size-main-btn_more: 14px;
  --font-size-main-btn_shopping: 14px;
  --pc-font-size-main-btn_shopping: 14px;
  --pc-font-size-categories: 18px;
  --pc-font-size-main_header: 48px;
  --pc-font-size-main-categories_headers: 14px;
  --alternative-text: #b4afaf;
  --caritas-txt: #fff;
  --shopping-list-border: rgba(79, 46, 232, .4);
  --main-font-family: "DM Sans", sans-serif;
  --transition-duration: 250ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  --amazon-logo: grayscale(100%);
  --icon-color: #111;
}

body {
  background-color: var(--body-background);
  font-family: DM Sans, sans-serif;
}

:root[data-theme="light"] {
  --main-white-color: #fff;
  --main-bc-modal: #fff;
  --main-yellow-color: #eac645;
  --accent-yellow-color: #eac645;
  --main-violet-color: #4f2ee8;
  --accent-violet-color: #4f2ee8;
  --main-dark_gray-color: #202024;
  --main-black-color: #111;
  --body-background: #f6f6f6;
  --alternative-text: #b4afaf;
  --caritas-txt: #fff;
  --shopping-list-border: rgba(79, 46, 232, .4);
  --main-font-family: "DM Sans", sans-serif;
  --transition-duration: 250ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  --amazon-logo: grayscale(100%);
  --icon-color: #111;
}

:root[data-theme="dark"] {
  --main-white-color: #111;
  --main-bc-modal: #202024;
  --main-yellow-color: #eac645;
  --accent-yellow-color: #eac645;
  --main-violet-color: #4f2ee8;
  --accent-violet-color: #4f2ee8;
  --main-dark_gray-color: #202024;
  --main-black-color: #fff;
  --body-background: #202024;
  --icon-color: #f6f6f6;
  --amazon-logo: brightness(0) invert(1);
}



